body{font-family:Arial, Helvetica, sans-serif;background-color:rgba(190,184,184,0.913);background-image:linear-gradient(25deg, #637bbc, #99a5d2, #ccd1e9, #fff);color:#444;font-size:12px}.description{padding-left:40px}.description h1{color:#555}.description p{font-size:1.2em;line-height:1.4;color:#444;width:60%}.description .description-bg{width:60%;background:#00000010;padding:1px 16px;border-radius:3px}.description .description-bg p{width:100%}.container{display:flex}.left-side{width:15%;padding:12px;padding-left:40px}.middle-side{width:50%;padding-left:40px}.right-side{width:35%;padding-left:40px}.segment-label{background:#6d68ab;border-radius:50px;color:#FFF;margin-bottom:12px;padding:12px;text-align:center;font-size:12px;font-weight:600}.bar{background:#BBDEFB}.right-footer{display:flex;justify-content:flex-end;padding-right:40px;padding-bottom:20px}.right-footer a{margin-left:15px;color:#423e72;font-size:15px}.right-footer a:hover{color:#312b5a}
